









































































import { Component, Emit, Prop, Vue } from 'vue-property-decorator';
import { Token } from '@/model/types';
import AmountInput from '@/components/AmountInput.vue';
import ActionButton from '@/components/ActionButton.vue';
import RaidenDialog from '@/components/RaidenDialog.vue';
import { BalanceUtils } from '@/utils/balance-utils';

@Component({
  components: {
    AmountInput,
    ActionButton,
    RaidenDialog
  }
})
export default class ChannelDepositDialog extends Vue {
  @Prop({ required: true })
  identifier!: number;
  @Prop({ required: true, type: Boolean, default: false })
  visible!: boolean;
  @Prop({ required: true })
  token!: Token;
  @Prop({ required: true })
  loading!: boolean;
  @Prop({ required: false, default: false })
  done?: boolean;

  deposit: string = '0.0';
  valid: boolean = false;

  @Emit()
  cancel() {}

  depositTokens() {
    const deposit = BalanceUtils.parse(this.deposit, this.token.decimals!);
    if (!deposit.isZero()) {
      this.$emit('depositTokens', deposit);
    }
  }
}
